

const chatAlarm = function(message, dispatch) {
    if (message !== null) {
        dispatch({type: "SWITCH_ALARM"});
        console.log(' ON');
        setTimeout(() => {
            dispatch({type: "SWITCH_ALARM"});
            console.log(' OFF');
        }, 2000)
    } else {
        return console.log(' ')
    }
}

export default chatAlarm