import axios from 'axios';

  //  .
  const logoutFn = function(history) {
    axios.post(`${axios.defaults.baseURL}/api/auth/logout`, { withCredentials: true })
    .then(res => {
      localStorage.clear();
        console.log(res.data.message);
        history.replace('/');
    })
    .catch(err => {
      console.log(err);
      console.log('  .');
      history.replace('/');
    })
  }

export default logoutFn